.section.pair {
    background: linear-gradient(#1c1e0e,#504f21);
    padding: 5rem 0;
}
.section .section-header {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    margin-bottom: 2rem;
    padding: 0 1rem;
}

.section .section-header .section-icon {
    margin-bottom: 1rem;
}
.section .section-header form{
    margin-top: 25px;
}
.button.steam {
    background-color: #688b31;
}
.button.steam:hover:not(.disabled) {
    background-color: #9ec362;
}
.button i {
    margin-right: 0.5rem;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media screen and (min-width: 769px){
    .section.pair .pair-steps {
        padding: 0;
    }
    .section.pair .pair-steps .step {
        flex-direction: row;
    }
    .section.pair .pair-steps .step .step-number {
        padding: 3rem;
    }
    .section.pair .pair-steps .step .step-body {
        padding: 3rem;
    }
    .section.pair .pair-steps .step .step-body .step-footer form {
        flex-direction: row;
    }
    .section.pair .pair-steps .step .step-body .step-footer form .button {
        width: auto;
    }
    .section.pair .pair-steps .step .step-body .step-footer .subtext {
        margin: 0 1rem;
    }
}