.footer{
  background-color: rgba(0,0,0,.2);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 4rem 0;
}
.footer-container {
  align-self: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 2rem;
}
.footer-container a {
  margin-bottom: 1.5rem;
  opacity: .7;
  transform: translateY(0);
  transition: all .2s ease;
}
.footer-container a:hover {
    opacity: 1;
    transform: translateY(-5px);
}

.footer-container a img {
    max-width: 200px;
}
.footer-copyright{
  color: rgba(239,235,224,.3);
}


@media screen and (min-width: 769px){
  .footer-container {
    flex-direction: row;
  }
  .footer-container a {
    margin-bottom: 0;
  }
  .footer-container a:last-child {
    margin-left: 3rem;
  }
}

