.drops-hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 3rem;
    padding: 2rem 1rem;
    overflow: hidden;
}

.drops-hero__logo {
    display: flex;
    justify-content: center;
    margin-bottom: 4rem;
}

.drops-hero__logo .rust-logo {
    animation-duration: .75s;
    align-items: flex-start;
    align-self: center;
    max-width: 160px;
    opacity: 0;
    width: 160px;
    height: auto;
}

.drops-hero .twitch-badge {
    display: flex;
    align-items: center;
    justify-content: center;
    animation-delay: .2s;
    animation-duration: 1s;
    opacity: 0;
}

.drops-hero .drops_hero__body {
    width: 100%;
}

.drops-hero_twitch-under{
    position: absolute;
    bottom: -2px;
    scale: 1.6;
}

.hero-fundraise {
    display: flex;
    flex-direction: column;
    background-color: #c7c944;
    box-shadow: 0 5px 20px rgb(88 93 39 / 50%);
    padding: 1rem 2rem;
    animation-duration: 1s;
    animation-delay: .3s;
    margin-bottom: 2rem;
    opacity: 0;
    gap: 10px;
}

.drops-hero .drops-hero-footer {
    position: sticky;
    display: flex;
    flex-direction: row;
    opacity: 0;
    animation-delay: .5s;
    animation-duration: 1s;
    top: 0;
}

.hero-fundraise .amount {
    display: flex;
    flex-direction: column;
    flex: 1;
    align-items: center;
    justify-content: center;
}
.hero-fundraise .amount .amount-title {
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 2px;
    color: #46483c;
    font-size: .75rem;
    margin-top: 4px;
}

.hero-fundraise .donate-amount {
    color: #000;
    text-shadow: 0 2px 0 rgb(0 0 0 / 20%);
    font-size: 2rem;
    font-weight: bold;
    flex: 1;
}

.hero-fundraise .donate-button {
    display: inline-flex;
    background-color: #f4ff46;
    align-self: center;
    box-shadow: 0 5px 5px rgb(0 0 0 / 0%);
}

.drops-hero .drops-hero-footer {
    position: sticky;
    display: flex;
    flex-direction: row;
    opacity: 0;
    animation-delay: .5s;
    animation-duration: 1s;
    top: 0;
}

.drops-hero .twitch-badge img {
    width: 100%;
    margin-bottom: 4rem;
}

.tabs {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}

.tabs .tab {
    border: 2px solid #a9ab3c;
    background: linear-gradient(rgb(245 255 103 / 50%),rgb(247 255 125 / 40%));
    border-radius: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 1.5rem;
    transition: all .2s ease;
    margin-bottom: 4px;
}
.tabs .tab:hover {
    background: linear-gradient(rgb(239 255 111 / 50%),rgb(173 175 71 / 80%));
    box-shadow: 0 5px 10px rgb(0 0 0 / 20%);
    z-index: 1;
}
.tabs .tab:hover span {
    color: #000;
}

.tabs .tab.is-active {
    background: linear-gradient(to top,rgb(243 255 143 / 90%),rgb(229 241 41 / 70%));
}
.tabs .tab.is-active:hover {
    background: linear-gradient(to top,#edff8c,rgb(252 255 41 / 80%));
}

.tabs .tab span {
    color: rgba(0,0,0,.7);
    font-weight: bold;
    font-size: 1.25rem;
    text-transform: uppercase;
    text-shadow: 0 1px 0 rgb(0 0 0 / 10%);
    transition: all .2s ease;
}
.tabs .tab.is-active span {
    color: #000;
}

.tabs .tab .mdi {
    color: rgba(0,0,0,.7);
    font-size: 24px;
    margin: 0 0 0 8px;
}
.tabs .tab.is-active .mdi {
    color: #000;
}
.mdi:before, .mdi-set {
    display: inline-block;
    font: normal normal normal 24px/1 "Material Design Icons";
    font-size: inherit;
    text-rendering: auto;
    line-height: inherit;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.mdi-parachute::before {
    content: "\F0CB4";
}
.mdi-account::before {
    content: "\F0004";
}

.section .section-header h1 {
    color: #fff;
    font-size: 2rem;
    text-transform: uppercase;
    margin-bottom: 0.5rem;
}
.section .section-header p span {
    color: #f0f5c2;
    font-weight: bold;
}
.section .section-header p {
    font-size: 1.25rem;
    color: rgb(242 245 194 / 70%);
    margin: 0;
    padding: 0;
}
.section .section-header .section-icon {
    margin-bottom: 1rem;
}
.section .section-header .section-icon img {
    max-height: 64px;
}
.section .section-footer {
    display: flex;
    align-items: center;
    justify-content: center;
}
.button.twitch {
    background-color: #d4d95d;
}
.button.twitch:hover:not(.disabled) {
    background-color: #f7ff46;
}

.section .faq-question {
    background-color: rgba(0,0,0,.3);
    padding: 2rem;
    margin-bottom: 1rem;
}
.section .faq-question h2 {
    margin-bottom: 1rem;
}
.section .faq-question p {
    padding: 0;
    margin: 0;
}
.section .faq-question a {
    color: #aaa634;
    font-weight: bold;
    transition: all .2s ease;
}

.section.timer-section{
    background: linear-gradient(#5c5d27,#aba946);
}

.timer{
    display: flex;
    flex-direction: column-reverse;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    gap: 20px;
}
.timer .status{
    width: fit-content;
    padding: 3px;
    border: 2px solid red;
    border-radius: 5px;
    background-color: rgb(255 0 0 / 50%);
    box-shadow: 0px 0px 10px 4px rgb(255 0 0 / 30%);
    margin: 0 auto;
}
.timer .status span{
    color: #fff;
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.timer .status span::before{
    content: '';
    display: inline-block;
    width: 10px;
    height: 10px;
    margin-right: 5px;
    -moz-border-radius: 7.5px;
    -webkit-border-radius: 7.5px;
    border-radius: 7.5px;
    background-color: red;
}
.timer .status+h1{
    margin: 15px auto;
    color: #fff;
    text-align: center;
}
.timer .texts-block .dates{
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.timer .texts-block .dates span:not(.date-block){
    font-size: 15px;
    margin: 0px 10px;
    color: #000;
    font-weight: bold;
    letter-spacing: 2px;
}
.timer .texts-block .dates .date-block{
    background-color: #000000bf;
    padding: 4px 8px;
    font-weight: bold;
    color: #fff;
}

.timer .timer-block{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.timer .timer-block h5{
    width: 100%;
    text-align: center;
    background-color: #d4d95d;
    color: #000;
    padding: 5px 0px;
}
.timer .timer-timer{
    display: flex;
    align-items: center;
    background-color: #272912;
}
.timer .timer-timer .timer-section{
    padding: 20px 25px;
    text-align: center;
    color: #fff;
} 
.timer .timer-timer .timer-section h1{
    color: #fff;
}
.timer .timer-timer .timer-section p{
    margin: 0;
    font-size: 10px;
    color: #d7d54a;
}

.cards-container{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 25px;
}

.cards-container .card{
    width: 250px;
    height: 450px;
    background-color: #9c9a4050;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
}

.card .card-header{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #9c9a40;
    padding: 10px;
}
.card .card-header .header{
    display: flex;
    align-items: center;
}
.card .card-header .header img{
    width: 25px;
    height: 25px;
    margin-right: 5px;
}
.card .card-header .header p{
    margin: 0;
    color: #fff;
    font-size: 14px;
    font-weight: bold;
}
.card .card-header .status{
    width: fit-content;
    padding: 3px;
    border: 2px solid red;
    border-radius: 5px;
    background-color: rgb(255 0 0 / 50%);
    box-shadow: 0px 0px 10px 4px rgb(255 0 0 / 30%);
}
.card .card-header .status span::before {
    content: '';
    display: inline-block;
    width: 10px;
    height: 10px;
    margin-right: 5px;
    -moz-border-radius: 7.5px;
    -webkit-border-radius: 7.5px;
    border-radius: 7.5px;
    background-color: red;
}
.card .card-header .status span {
    color: #fff;
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: capitalize;
}

.card .card-body{
    width: 100%;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px 15px;
    box-sizing: border-box;
}
.card .card-body img{
    max-width: 100%;
    max-height: 250px;
    margin: 0px auto;
}
.card .card-footer{
    width: 100%;
    height: 65px;
    text-align: center;
    background-color: #9c9a40;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.card .card-footer h5{
    color: #fff;
    padding: 5px;
    text-transform: uppercase;
}

@media screen and (min-width: 769px){
    .drops-hero {
        margin: 3rem 0 8rem 0;
        min-height: 0;
    }

    .drops-hero__logo {
        align-items: center;
        margin-bottom: 2rem;
        flex: 1;
    }

    .drops-hero .twitch-badge {
        margin-bottom: 0;
    }

    .drops-hero .twitch-badge img {
        width: 100%;
    }

    .drops-hero .drops_hero__body {
        width: auto;
    }

    .hero-fundraise {
        align-items: center;
        justify-content: center;
        flex-direction: row;
        min-width: 500px;
        width: auto;
    }

    .hero-fundraise .amount {
        margin-bottom: 0;
        align-items: flex-start;
        justify-content: flex-start;
    }

    .timer .status{
        margin: unset;
    }
    .timer .status+h1{
        text-align: left;
    }
    .timer .texts-block .dates{
        justify-content: flex-start;
    }

    .tabs {
        flex-direction: row;
    }

    .tabs .tab {
        border-right: 0;
    }

    .tabs .tab:last-child {
        border-right: 2px solid #a9ab3c;
    }

    .section .section-header h1 {
        font-size: 3.5rem;
    }

    .timer{
        flex-direction: row;
    }
}