@font-face{
  font-family: "Roboto Condensed";
  src: url("./fonts/RobotoCondensed-Regular.ttf");
}

*, *:before, *:after {
    box-sizing: inherit;
}

a {
    text-decoration: none;
}


body {
  margin: 0;
  display: flex;
  place-items: center;
  min-width: 320px;
  min-height: 100vh;
}


body{
  display: block;
  color: rgba(239,235,224,.7);
    background-color: #121414;
    background-image: url(./img/backgroundSim-mobile.png),url(./img/bg-body.jpg)!important;
    background-repeat: no-repeat,repeat;
    background-position: center top;
    font-family: "Roboto Condensed",sans-serif;
    font-size: 16px;
    padding: 0;
    margin: 0;
}

img {
    overflow-clip-margin: content-box;
    overflow: clip;
}

p, span {
    color: rgba(239,235,224,.7);
}

h1, h2, h3, h4, h5 {
    color: #efebe0;
    padding: 0;
    margin: 0;
}

@media screen and (min-width: 769px){
  body {
    background-size: contain;
    background-image: url(./img/backgroundSim.png),url(./img/bg-body.jpg)!important;
  }
}


.animate__fadeInDown {
    -webkit-animation-name: fadeInDown;
    animation-name: fadeInDown;
    animation-fill-mode: forwards;
}

.button {
    align-self: flex-start;
    border: 0;
    border-radius: 6px;
    color: #000000;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: inherit;
    font-size: 1rem;
    font-weight: bold;
    padding: 1rem 1.5rem;
    text-transform: uppercase;
    text-shadow: 0 1px 0 rgb(0 0 0 / 20%);
    transition: all .2s ease;
    max-height: 55px;
    text-align: center;
}

.section {
    display: flex;
    flex-direction: column;
    border-top: 2px solid #d4d95d!important;
}
.section.infobar {
    background: linear-gradient(#1c1e0e,#504f21);
    padding: 4rem 0;
    position: relative;
}
.section.is-large {
    padding: 3rem 0;
}

.section .section-header {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    margin-bottom: 2rem;
    padding: 0 1rem;
}

.section .section-body {
    padding: 1rem 2rem;
}


.container {
    flex-grow: 1;
    margin: 0 auto;
    position: relative;
    width: 100%;
}

@media screen and (min-width: 1024px){
  .section.is-large {
      padding: 6rem 0;
  }
  .container {
    max-width: 960px;
  }
  .section .section-header {
    margin-bottom: 3rem;
  }
  .section .section-body {
    padding: 3rem;
  }
}

@media screen and (min-width: 1204px){
  .container {
      max-width: 1140px;
  }
}

@keyframes fadeInDown{
  0%{
    opacity: 0;
    -webkit-transform: translate3d(0,-50px,0);
    transform: translate3d(0,-50px,0);
  }
  100%{
    opacity: 1;
    -webkit-transform: translate3d(0,0,0);
    transform: translate3d(0,0,0);
  }
}